.topbar{
    width: 100%;
    box-sizing: border-box;
    height: 60px;
    background-color: #fff;
    box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.1);
    position: absolute;
    top: 0;
    display: flex;
    align-items: center;
    padding:30px 30px;
    justify-content: space-between;
    position: fixed;
}
.topbar img{
    width: 150px;
}