.mainCard{
    width: calc(33.3% - 26px);
    margin: 12px;
    border: 1px solid gray;
    border-radius: 5px;
    height: 500px;
    overflow:hidden;
    word-break: keep-all;
    max-height: 500px;
    text-overflow: ellipsis;
    cursor: pointer;
    
}
.imageSection{
    height: 300px;
}
.imageSection img{
    width: 100%;
    height: 300px;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
}
.textSection{
    padding:20px;
    
    height: 200px;
    overflow: hidden;
    text-overflow: ellipsis;
   

}
.textSection p{
    font-size: 15px;
    margin-top: 15px;
    color: grey;
}
.textSection h2{
    margin-bottom: 15px;
}